body {
  font: 100% Arial, Sans Serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.header {
  display: flex;
  justify-content: flex-end;
}

.header > button:focus {
  outline: none;
}

#background-wrap {
  bottom: 0;
  left: 0;
  padding-top: 50px;
  overflow-x: hidden;
  right: 0;
  top: 0;
  z-index: -1;
}

h1 {
  font-size: 10vw;

  color: rgb(45, 175, 190);
}

.title-holder {
  display: flex;

  justify-content: center;
  align-items: center;
  height: 10vh;
  margin-top: 40vh;
}
p {
  font-size: 2vw;
}

.sun {
  display: inline-block;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  background: orange;
  box-shadow: 0 0 10px orange, 0 0 60px orange, 0 0 200px yellow,
    inset 0 0 80px yellow;
  z-index: 1;
  overflow: hidden;
  margin: 20px;
}
.sun-container {
  display: flex;
  align-items: center;
  z-index: -10;
}

.cloud {
  margin-top: 70px;
  width: 350px;
  height: 120px;
  background: #eceff1;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
  animation: move 15s linear infinite alternate;
  position: absolute;
  z-index: 1;
}

.cloud::after,
.cloud::before {
  content: "";
  position: relative;
  display: inline-block;
  background: inherit;
  border-radius: inherit;
}
.cloud::after {
  width: 100px;
  height: 100px;
  top: -130px;
  left: -120px;
}
.cloud::before {
  width: 180px;
  height: 180px;
  top: -70px;
  left: 130px;
}

.cloud-gray {
  background-color: rgb(44, 66, 66);
}

@keyframes move {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100px);
  }
}

