.gray-cloud {
  background-color: rgb(32, 31, 31);
  animation: move 16s linear infinite alternate;
}
.gray-cloud1 {
  background-color: rgb(19, 19, 19);
  animation: move 20s linear infinite alternate;
}

.moon-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  z-index: -20;
}

.moon {
  display: inline-block;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  background: #eceff1;
  box-shadow: 0 0 10px #eceff1, 0 0 60px #eceff1, 0 0 200px #eceff1,
    inset 0 0 80px #eceff1;
  z-index: 1;
  overflow: hidden;
  margin: 20px;
}
a {
  text-decoration: none;
  color: inherit;
  background: none;
  border: none;
  cursor: pointer;
  color: rgb(0, 109, 143);
  font-size: 1em;
  margin-left: 1em;
  margin-right: 0.5em;
}
.gray-cloud-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  z-index: -10;
}
.gray-cloud-container1 {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.description-holder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  text-align: center;
  margin-top: 1em;
  margin-bottom: 100vh;
}

.description {
  margin-top: 70px;
  font-size: 1.5em;
  font-weight: 700;
  color: rgb(49, 104, 110);
}

.moon-2 {
  height: 150px;
  width: 150px;
}
